.card{
    width: 20rem;
    height: 30rem;
    border: 1px solid #000000;
    margin-bottom: 1.5rem;
    background: url('../../assets/banner/project.jpg');
    background-size: cover;
    background-position-x: center;
}

.card .card-body{
    display: inline-block;
}
.card .card-body .card-title{
    color: #ffffff;
}

/* details project */
.card .card-text{
    color: blanchedalmond;
    margin-right: 1rem;
}
.card .card-link{
    margin-right: 1rem;
    color: #ffffff;
}
.card-body i{
    margin-left: .5rem;
}
.card .card-body .card-subtitle{
    position: absolute;
    bottom: 5%;
    color: #ffffff;
    font-style: italic;
}
.card:hover{
  animation: effectPublication .3s forwards;
  cursor: pointer;
}

@keyframes effectPublication{
    100%{
        transform: scale(1.1);
        box-shadow: 1px 1px 30px black;
    }
    
}
  